import React, { Component } from "react";
import propType from "prop-types";

export default class Clock extends Component {
    static propType = {
        deration: propType.number.isRequired
    }

    timeToHMS = time => {
    const h = Math.floor(time / 3600000),
        m = Math.floor(time % 3600000 / 60000 ),
        s = Math.floor(time % 3600000 % 60000 / 1000 );
    return (h > 0 ? (h < 10 ? "0" + h : h) + ":" : "00:") + (m > 0 ? (m < 10 ? "0" + m : m) + ":" : "00:") + (s > 0 ? (s < 10 ? "0" + s : s) : "00");
    }

    render(){
        return (
            <div>
            <h1 className="component-title">番茄钟</h1>
            <hr />
            <h1 className="clock-view">{this.timeToHMS(this.props.deration)}</h1>
            <hr/>
            </div>
        )
    }

}


