import React, { Component } from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TodoTextInput from './TodoTextInput'
import ControlGroupContainer from './../../containers/TomatoClock/ControlGroupContainer';


export default class TodoItem extends Component {
  static propTypes = {
    todo: PropTypes.object.isRequired,
    editTodo: PropTypes.func.isRequired,
    deleteTodo: PropTypes.func.isRequired,
    onCompleted: PropTypes.func.isRequired
  }

  state = {
    editing: false
  }

  handleDoubleClick = () => {
    this.setState({ editing: true })
  }

  handleTouchStart = () => {
    this._timer = setTimeout(() => {
      this.setState({ editing: true })
    }, 1000);
  }

  handleTouchCancel = () => {
    clearTimeout(this._timer)
  }

  handleSave = (id, text) => {
    if (text.length === 0) {
      this.props.deleteTodo(id)
    } else {
      this.props.editTodo(id, text)
    }
    this.setState({ editing: false })
  }

  render() {
    const { todo, deleteTodo, onCompleted } = this.props
    let element
    if (this.state.editing) {
      element = (
        <TodoTextInput text={todo.text}
                       editing={this.state.editing}
                       onSave={(text) => this.handleSave(todo.id, text)} />
      )
    } else {
      element = (
        <div className="view">
          <input className="toggle"
                 type="checkbox"
                 checked={todo.completed}
                 onChange={() => onCompleted(todo.id)} />
          <label onDoubleClick={this.handleDoubleClick}
                 onTouchStart={this.handleTouchStart}
                 onTouchCancel={this.handleTouchCancel}>
            {todo.text}
          </label>
          <div className="destroy"
                  onClick={() => deleteTodo(todo.id)} />
          {!todo.completed ? <ControlGroupContainer todoStyle id={todo.id} onDoubleClick={this.handleDoubleClick}/> : null}
        </div>
      )
    }

    return (
      <li className={classnames({
        completed: todo.completed,
        editing: this.state.editing
      })}>
        {element}
      </li>
    )
  }
}



